import React, { useState, useEffect } from 'react'
import QRCodeLinkGenerator from '../common/QRCodeLinkGenerator'
import { useSelector } from 'react-redux'
import { API_URL, BlockPopup, IMAGE_URL, SITE_LINK, ToastPopup } from '../../actions/constant'
import axios from 'axios'

const ViewQrCode = () => {
    const { user } = useSelector(state => state.auth)
    const [data, setdata] = useState({})

    useEffect(() => {
        getData()
    }, [])

    const getData = async () => {
        BlockPopup.standard('.invoicesetting', `Please wait... Don't Close`);
        try {
            let result = await axios.get(API_URL + "/api/usersetting/")
            BlockPopup.remove('.invoicesetting');
            if (result.data) {
                setdata(result.data)
            }

        } catch (err) {
            BlockPopup.remove('.invoicesetting');
            ToastPopup.failure("Error Ocuured in API")
        }
    }

    var logoImg = "assets/images/logo.png"
    if (data.qrLogo) {
        logoImg = IMAGE_URL + data.qrLogo
    }

    console.log("SITE_LINK + `/viewqrcode/${user.id}`", SITE_LINK + `/viewqrcode/${user.id}/${user.walletAddress}`)
    return (
        <div>
            <>

                <div
                    id="viewqrcodeModal"
                    className="modal fade invoicesetting"
                    tabIndex={-1}
                    role="dialog"
                    aria-labelledby="myModalLabel"
                    aria-hidden="true"
                >

                    <div className="modal-dialog modal-sm">

                        <div className="modal-content">
                            <div className="modal-body">

                                <div>
                                    <div>
                                        <div className="row g-0">
                                            <div className="col-md-12">
                                                <div className="p-xl-5 p-3">


                                                    <div className="text-center mb-2">

                                                        <img
                                                            src={logoImg}
                                                            className="align-self-center"
                                                            alt="logo-img"
                                                            height={100}
                                                            style={{ height: 70 }}
                                                        />

                                                    </div>

                                                    <h3 className="h3 mb-0 mt-3 text-center">{data.qrName}</h3>
                                                    <p className="text-muted mt-1 mb-4 text-center">
                                                        Scan QR code and make the payment
                                                    </p>
                                                    <div className='text-center'>
                                                        <QRCodeLinkGenerator
                                                            link={SITE_LINK + `/viewqrcode/${user.id}/${user.walletAddress}`}
                                                            // tokenAmount="100"

                                                            size={170}
                                                        />
                                                    </div>

                                                    <div className='text-center' >
                                                        <img
                                                            src="assets/images/poweredby.png"
                                                            className="align-self-end"
                                                            alt="logo-img"
                                                            height={40}
                                                        />
                                                    </div>

                                                </div>
                                            </div>

                                        </div>
                                    </div>{" "}
                                    {/* end card-body */}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>

        </div>
    )
}

export default ViewQrCode
